@font-face {
  font-family: "Oswald Regular";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Oswald-Regular.woff2") format("woff2"), url("../fonts/Oswald-Regular.woff") format("woff");
}
@font-face {
  font-family: "Oswald Medium";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/Oswald-Regular.woff2") format("woff2"), url("../fonts/Oswald-Regular.woff") format("woff");
}
.page {
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  color: #000000;
}

.page__body {
  min-height: 100%;
  margin: 0;
  background-color: #ffffff;
  color: #000000;
}

.main-header {
  background-color: #ffffff;
  color: #000000;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
@media (max-width: 767px) {
  .main-header {
    position: relative;
  }
}

.main-header__logo {
  height: 38px;
}
@media (min-width: 768px) {
  .main-header__logo {
    height: 52px;
    width: 174px;
  }
}
@media (min-width: 1440px) {
  .main-header__logo {
    height: 59px;
    width: 202px;
  }
}

.main-header__toggle {
  background-color: #ffffff;
  border: none;
}
@media (min-width: 768px) {
  .main-header__toggle {
    display: none;
  }
}

@media (max-width: 767px) {
  .main-nav {
    display: flex;
    justify-content: center;
    width: 100%;
    position: absolute;
    top: 40px;
    z-index: 1;
  }
}

.main-nav-list {
  width: 100%;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  text-align: center;
  display: none;
}
@media (min-width: 768px) {
  .main-nav-list {
    width: 440px;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
  }
}

.main-nav-list__link {
  width: 100%;
  padding: 22px 0 24px 0;
  color: #000000;
  background-color: #ffffff;
  font-size: 20px;
  font-style: normal;
  font-weight: 500;
  line-height: 20px;
  text-transform: uppercase;
  display: block;
  text-decoration: none;
}

.main-nav-list__item--current {
  text-decoration: underline;
  text-underline-offset: 10px;
  text-decoration-color: #68b738;
}

.functional-food {
  position: relative;
  height: 480px;
}
@media (max-width: 767px) {
  .functional-food {
    color: #ffffff;
    text-align: center;
  }
}

@media (max-width: 767px) {
  .functional-food__wrapper {
    height: 302px;
    display: flex;
    flex-direction: column;
    background-image: url("../../img/background-green.png"), url("../../img/index-background-image.png");
    background-color: rgba(104, 183, 56, 0.85);
  }
}

.functional-food__title {
  margin: 27px 0 0;
  padding: 0;
}

.functional-food__text {
  margin: 25px 0 0;
  padding: 0;
}

.functional-food__image {
  position: absolute;
  left: 50%;
  margin-left: -140px;
  top: 64%;
  margin-top: -140px;
}

.functional-food__button {
  font-family: "Oswald Regular", "Arial", sans-serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  text-transform: uppercase;
  color: #ffffff;
  padding: 11px 0;
  background-color: #68b738;
  border: none;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}